body{
    background-color: black;
}
@media (min-width:640px) {
    ::-webkit-scrollbar {
    display: none;
    }
}
.divapp{
    height: -webkit-max-content;
    height: max-content;
}
.copyright-text{
    top: 50%;
    font-size: 15px;
    -webkit-transform: translateY(-50%) rotate(-90deg);
            transform: translateY(-50%) rotate(-90deg);
    -webkit-text-stroke: .02em #000;
    text-stroke: 1px #000;
    z-index: 999;
}

body{
    font-family: 'Nunito', sans-serif;
}

.logo {
    font-family: 'Dela Gothic One', cursive;
}

.stroke{
    -webkit-text-stroke: .02em #fff;
    text-stroke: 1px #fff;
    color: transparent;
    
}
.img-face{
    width: 300px;
    height: 300px;
    margin-top: 10px;
    box-shadow:          0 0 25px rgba(114, 114, 114, 0.774);
    border-radius: 150px;
    border: 4px;
    padding: 0px;
   
}
.name{
    font-family: 'Dela Gothic One', cursive;
}
.text-shine/* :hover */{
    background: linear-gradient(to right, rgba(183,33,238,1) 20%, rgba(85,47,179,1) 52%, rgba(29,94,225,1) 80%,rgba(183,33,238,1) 100%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-size: 200% auto;
    -webkit-animation: textShine 2.3s ease-in-out infinite alternate;
            animation: textShine 2.3s ease-in-out infinite alternate;
}

.text-shine:hover{
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-animation: textShine 8s ease-in-out infinite alternate;
            animation: textShine 8s ease-in-out infinite alternate;
}
.particles {
    z-index: -1;
}
.technologies{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 16px;
}
.patron{
    top: 4.5em;
}

@media (max-width:764px){
    .contact, .curriculum{
        display: none;
    }
} 

@media (max-width:764px) {
    .present{
        position: relative;
        width: 30%;
    }
    .img-box{
        top: 6em; 
    }
    .patron{
        display: none;
    }
    .megabox{
        margin-top: 10em;
        margin-left: 0;
        margin-right: 0;
    }
    div.allb{
        margin-right: 0px;
        margin: 0px !important;
    }
    .textpro{
        justify-items: right;
    } 
    .img-face{
        margin-top: 20px;
        margin-bottom: 20px;
        margin-right: 20px;
    }
    .separador{
        box-shadow: 2px 2px 50px 3px rgba(183,33,238,1);
    }
}
@-webkit-keyframes textShine{
    100% {
    background-position: 200%;
    }
}
@keyframes textShine{
    100% {
    background-position: 200%;
    }
}
